import { useState, useEffect } from 'react'
import { inject, observer } from 'mobx-react'
import { formatAddress, eqInNumber } from '../../utils/utils'
import { useAlert } from 'react-alert'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import Button from '../../components/Button/Button'
import apiProxy from '../../model/ApiProxy'
import timeLogo from './img/time.svg'
import tokenLogo from './img/pToken.svg'
import './airdrop.less'
function AriDrop({ lang, wallet = {}, type = 'deri' }) {
  const [balance, setBalance] = useState(0);
  const [disabled, setDisabled] = useState(true);
  const alert = useAlert();
  const [info, setInfo] = useState({})
  const [isWhite, setIsWhite] = useState(false)
  const [isPToken, setIsPToken] = useState(true)
  const [isExist, setIsExist] = useState(true)
  const getBalance = async () => {
    if (wallet.isConnected()) {
      let res = await apiProxy.request('getDeriBalance', [wallet.detail.chainId, wallet.detail.account])
      setBalance(res)
    }
  }
  const getInfo = async () => {
    let info = await apiProxy.request('getUserInfoAllForAirDrop', [wallet.detail.account])
    let deadline = info.deadline * 1000
    setInfo(info)
    setDisabled(!info.valid)
    let timestamp = new Date()
    if (timestamp.getTime() >= deadline) {
      setDisabled(true)
    }
    if (timestamp.getTime() >= 1647000000000) {
      setDisabled(true)
    }
  }

  const click = async () => {
    if (type === 'pToken') {
      let res = await apiProxy.request('airdropPTokenV3', [wallet.detail.chainId, wallet.detail.account], { includeResponse: true })
      if (!res.success) {
        alert.error(lang['claim-failed'])
      }
      if (res.success) {
        alert.success('Claim Airdrop', {
          timeout: 5000,
          isTransaction: true,
          transactionHash:res.response.data.transactionHash,
          link: `${wallet.blockExploreUrl}/tx/${res.response.data.transactionHash}`,
          title: 'Claim Airdrop Executed'
        })
        setDisabled(true)
      }
    } else {
      if (!info.valid) {
        alert.error(lang['no-deri-to-claim']);
        return;
      }
      if (!eqInNumber(wallet.detail.chainId, info.chainId)) {
        alert.error(lang['please-switch-to-BSC-to-claim-deri'])
        return;
      }
      let res = await apiProxy.request('mintAirdrop', [info.chainId, wallet.detail.account], { includeResponse: true })
      if (!res.success) {
        alert.error(lang['claim-failed'])
      }
      if (res.success) {
        setDisabled(true)
      }
      getBalance()
    }

  }

  const isUserInPTokenWhiteListV3 = async () => {
    let res = await apiProxy.request('isUserInPTokenWhiteListV3', [wallet.detail.chainId, wallet.detail.account])
    setIsWhite(res)
  }
  const isPTokenAirdroppedV3 = async () => {
    let res = await apiProxy.request('isPTokenAirdroppedV3', [wallet.detail.chainId, wallet.detail.account])
    setIsPToken(res)
  }

  const isUserPTokenExistV3 = async () => {
    let res = await apiProxy.request('isUserPTokenExistV3', [wallet.detail.chainId, wallet.detail.account])
    setIsExist(res)
  }

  useEffect(() => {
    if (wallet.isConnected()) {
      getBalance()
      if (type === 'pToken') {
        isUserInPTokenWhiteListV3()
        isPTokenAirdroppedV3()
        isUserPTokenExistV3()
      } else {
        getInfo()
      }
    }

  }, [wallet, wallet.detail, type])

  useEffect(() => {
    if (isWhite && !isExist && !isPToken) {
      let timestamp = new Date()
      if (timestamp.getTime() >= 1649757600000) {
        setDisabled(true)
      } else {
        setDisabled(false)

      }
    }
  }, [isWhite, isExist, isPToken])
  return (
    <div className='airdrop-box'>
      <div className='airdrop-info'>
        <div className='airdrop-info-box'>
          <div className='airdrop-title'>
            {lang['title']}
          </div>
          <div className='wallet-address'>
            <span className='blue-logo'></span>
            {formatAddress(wallet.detail.account)}
          </div>
          <div className='wallet-balance'>
            {lang['wallet-balance']} : <DeriNumberFormat value={balance} decimalScale={0} thousandSeparator={true} /> DERI
          </div>
          {type === 'pToken' && <div className='airdrop-time position-text'>
            <div className='time-logo'>
              <img src={tokenLogo} alt='' />
            </div>
            <div className='time-text'>
              <div className='time-title'>
                <div>
                  Get free mBTC^2 perpetual
                </div>
                <div>
                  powers position
                </div>
              </div>
            </div>
          </div>}
          {type === 'pToken' && <div className='airdrop-time position'>
            <div className='time-logo'>
              <img src={timeLogo} alt='' />
            </div>
            <div className='time-text'>
              <div className='time-title'>
                <div>
                  {lang['start-time']}
                </div>
                <div>
                  {lang['expire-time']}
                </div>
              </div>
              <div>
                <div>
                  2022-04-08 10:00:00 UTC
                </div>
                <div>
                  2022-04-12 10:00:00 UTC
                </div>
              </div>
            </div>
          </div>}
          {type === 'deri' && <div className='airdrop-time'>
            <div className='time-logo'>
              <img src={timeLogo} alt='' />
            </div>
            <div className='time-text'>
              <div className='time-title'>
                <div>
                  {lang['start-time']}
                </div>
                <div>
                  {lang['expire-time']}
                </div>
              </div>
              <div>
                <div>
                  2022-03-09 10:00:00 UTC
                </div>
                <div>
                  2022-03-11 12:00:00 UTC
                </div>
              </div>
            </div>
          </div>}

          <div className='claim'>
            <Button fontWeight='bold' disabled={disabled} label={lang['claim']} width='277' height='72' fontSize='32' onClick={click} />
          </div>
        </div>
      </div>
    </div>
  )
}
export default inject('wallet', 'poolManager')(observer(AriDrop))